.mainContent {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("assets/imgs/mainImg.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
}

.mainContent>img {
    width: 100vw;
}

.aboutWrap {
    width: 100%;
    display: flex;
    background-color: #fff;
    height: 200px;
    align-items: center;
}

.aboutTitle {
    width: 25vw;
    font-size: 2rem;
    text-align: center;
    position: relative;
    text-decoration: dashed;
}

.aboutContext {
    width: 75vw;
    padding: 10px;
}

.aboutContextTitle {
    font-size: 1.5rem;
    font-weight: 700;
    margin: 10px 0px;
}


.workWrap {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #e0e0e0;
}

.workTitle {
    font-size: 2rem;
    text-align: center;
}

.workContext {
    width: 100%;
    max-width: 1024px;
}

.wokrs {
    padding: 30px 50px;
}

.bar {
    padding: 0 10px;
    color: #7d7d7d;
}

.companyNm {
    font-size: 1.5rem;
    font-weight: 700;
    color: rgb(46, 26, 26);
}

.workDt {
    font-size: 1.2rem;
    font-weight: 600;
}
.workingList > p {
    margin: 10px 0;
    word-break: keep-all;
}
.workingList {
    margin-left: 50px;
    font-size: 1.2rem;
    padding: 5px 0;
}

.work {
    font-size: 1rem;
    font-weight: 700;
}

.skillWrap {
    width: 100%;
    display: flex;
    background-color: rgb(231, 231, 231);
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.skillTitle {
    width: 100%;
    text-align: center;
    font-size: 2rem;
}

.skillContent {
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: center;
    padding: 20px;
}

.skillContent .skillList {
    width: 100%;
    display: flex;
    justify-content: center;
}

.skillBox>ul {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    list-style: none;
    align-items: center;
    justify-content: center;
    background-color: rgb(203, 203, 203);
    border-radius: 20px;
}

.skillBox>ul>li {
    width: 100%;
    border-radius: 20px;
    width: 150px;
    height: 150px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    margin: 10px;
    background-color: #ededed;
}

.iconHover {
    opacity: 0;
    text-align: center;
    line-height: 150px;
    height: 100%;
    width: 100%;
    color: #fff;
    font-size: 1.2rem;
    font-weight: 700;
    background-color: #191919;
    border-radius: 20px;
}

.iconHover:hover {
    opacity: 0.7;
}

.stackTitle {
    font-size: 1.2rem;
    font-weight: 700;
}